body{
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

.app{
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.header{
    background-color: #272727;
    display: flex;
    align-items: center;
    justify-content: space-between;    
    color: white;
    padding: 1em;
    box-sizing: border-box;
    height: 80px;
}

.header h2{
    color: rgb(255, 179, 38);
}

.content{
    box-sizing: border-box;
    background-color: #9797973d;
    padding: 1em;
    display: flex;
    flex-direction: column;
    flex: 1;
}

.personList{
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 1em;
    width: 100%;
    box-sizing: border-box;
}

.card{
    padding: 1em;
    border-radius: 5px;
    height: 60px;
    background-color: white;
    display: flex;
    align-items: center;
    color: black;
    justify-content: space-between;
    font-weight: bold;
    box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
    border: 1px solid #94949460;
    box-sizing: border-box;
}

.card:hover .deleteBtn{
    opacity: 1;
   
}
.info{
    width: 55%;
    display: flex;
    gap: 2em;
}

.name{
    flex: 1;
}

button{
    background-color: #272727;
    color: white;
    outline: none;
    border-radius: 5px;
    padding: 0.5em 1em;
    border: none;
    cursor: pointer;
}

.deleteBtn{
    opacity: 0;
    transition: opacity 0.8s ease-out;
}

.danger{
    background-color: crimson;
}

.success{
    background-color: rgb(0, 255, 106);
}

.personForm{
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 1em;
    width: 300px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
    border: 1px solid #94949460;
    gap: 0.5em;
    margin: 0.5em 1em;
    margin-bottom: 3em;
}

.row{
    display: flex;
    align-items: center;
}

.row span{
    flex: 1;
}

.row button{
    color: #272727;
    font-size: 14px;
    flex: 1;
    background: -webkit-linear-gradient(0deg, transparent 50%, #272727 50%);
    background-size: 300% 100%;
    border: 2px solid #272727;
    transition: 0.8s;
}

.row button:hover{
    color: rgb(255, 179, 38);
    background-position: -70% 0;
}

input{
    background-color: #9797973d;
    border: 1px solid #94949460;
    padding: 0.2em;
    border-radius: 3px;
    outline: none;
}

.header input{
    color: white;
}

.notification{
    border: 2px solid black;
    padding: 1em;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    font-weight: bold;
    width: 50%;
    box-sizing: border-box;
}

h2{
    margin: 0.5em 0.8em;
}

@media (max-width: 480px) {

    .card{
        height: 80px;
    }
    .info{
        flex-direction: column;
        gap: 10px;
    }
  }
